<template>
  <div class="logo">
    <h1>Nintendo Direct Flavour Logo Generator</h1>

    <div class="cover" id="cover">
      <div class="generator" id="generator">
        <span class="prefix input" contenteditable spellcheck="false">我动森</span>
        <!-- <span class="right-triangle"></span> -->
        <span class="postfix input" contenteditable spellcheck="false">直面会呢</span>
      </div>
    </div>

    <div class="download-share">
      <div class="download" @click="download">
        Export
      </div>
    </div>
  </div>
</template>

<script>
import domtoimage from 'dom-to-image'

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data () {
    return {
      prefix: '',
      postfix: ''
    }
  },
  methods: {
    // copy from https://github.com/bestony/logoly
    download () {
      const that = this
      const node = document.getElementById('cover')
      domtoimage.toPng(node)
      .then(function (res) {
        console.log(res)
        that.downloadIamge(res, 'logo')
      })
    },
    downloadIamge (imgSrc, name) {
      let image = new Image()
      image.setAttribute('crossOrigin', 'anonymous')
      image.onload = function () {
        let canvas = document.createElement('canvas')
        canvas.width = image.width
        canvas.height = image.height
        let context = canvas.getContext('2d')
        context.drawImage(image, 0, 0, image.width, image.height)
        let url = canvas.toDataURL('image/png')
        let a = document.createElement('a')
        let event = new MouseEvent('click')
        a.download = name || 'photo'
        a.href = url
        a.dispatchEvent(event)
      }
      image.src = imgSrc
    },
  }
}
</script>

